:root{
  font-family: Arial, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  height: 100vh;
  width: 100vw;
}

.main-container {
  position: relative;

}

.background-image {
  position: fixed;
  height: 100%;
  width: 100%;
  background-image: url(./images/background-image.png);
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(8px);
}

.contentLayout {
  color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 30px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 1.5rem 2rem;
  margin: 0 auto;

  line-height: 10rem;
  background-color: transparent;
}

h2 {
  text-align: center;
  font-family: Lobster Two;
  font-size: calc(2rem + 2vw);
  color:#bdb2ad;
  margin: 0;
  padding: 2vw;
  padding-bottom: 100px;
}

.topnav {
  display: flex;
  width: 500px;
  height: 40px;
  background: #444;
  background: rgba(0,0,0,.2);
  border-radius:  3px;
  border: 1px solid #fff;
  margin: 0 auto;
}

input[type=text] {
  padding: 0;
  border: 0;
  width: 370px;
  background: transparent;
}

input[type=text]:focus {
  outline: 0;
  background:transparent;
}


.topnav button {
    flex-grow: 1;
    border: 0;
    padding: 0;
    cursor: pointer;
    height: 40px;
    width: 120px;
    color: #fff;
    background: transparent;
    border-left: 1px solid #fff;
    border-radius: 0 3px 3px 0;

}

.topnav button:hover {
  border: 1px solid lightgreen;
  border-radius: 5px;
}

footer {
  color: white;
  position: fixed;
  top: 90vh;
  left: 40vw;
  font-size: min(2rem, 3vw);
}


.forecast-layout {
  text-align: center;
  color:white;
  font-size: min(3rem, 12vw);
  margin: 0 auto;
  padding-top: 30px;
  background-color: transparent;
  width: min(700px, 60vw);
  height: min(420px, 60vh);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}



.forecast-flex {
  max-width: 100%;
  max-height: 100%;
  padding-top: 7vh;
}

.flex-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
      justify-content: space-between;
  padding-bottom: 5px;
}


/* MEDIA SCREEN STYLES */

@media (max-width: 812px) 
   { 
    .forecast-layout {
      font-size: 1rem;
      min-width: 300px;
    }

    .topnav {
      width: 40vw;
      min-width: 190px;
}

input {
  width: 120px !important;
  flex-grow: 1;
}

button {
  width: 39px !important;
  flex-grow: 0;
}
   }